*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: $Regular, Arial, Helvetica, sans-serif;
  background: $color-dark;
}

.container {
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 1fr repeat(6, minmax(min-content, 18.8rem)) 1fr;
  grid-template-rows: 20vh 80vh repeat(4, min-content);
}
